.button {
     font: inherit;
     padding: 0.25rem 0.5rem;
     border: 1px solid #8b005d;
     color: white;
    margin: 0.5rem;
     background: #40005d;;
     box-shadow: 0 0 4px rgba(0, 0, 0, 0.26);
     cursor: pointer;
     width: 100%;
 }

.button:focus {
    outline: none;
}

.button:hover,
.button:active {
    background: #ac0e77;
    border-color: #ac0e77;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.26);
}

@media (min-width: 768px) {
    .button{
        width: auto;
    }
}